// flexbox
.flex-fill { flex: 1 1 0px; min-width: 0px; min-height: 0px; }
// misc
.hover\:opacity-100:hover { opacity: 1; }
.focus\:shadow-spread { box-shadow: 0 0 0 12px transparent; }
.focus\:shadow-spread:focus { box-shadow: 0 0 0 2px var(--color-blue-1); }
// typography
.list-none { list-style-type: none; padding: 0; margin: 0; }
.hover\:text-underline:hover { text-decoration: underline; }
.hover\:text-none:hover { text-decoration: none; }
.align-inherit { vertical-align: inherit; }
